<template>
  <div class="text_list_start">
    <div class="header">
      <div class="back" @click.stop="back()">
        <img src="../../assets/image/联合42.png" alt="">
      </div>
      <div class="testHead">在线考试</div>
    </div>
    <div class="contain">
      <div class="head">
        <img :src='head' alt="" v-if="isShow">
        <img src='../../assets/image/头像.png' alt="" v-if="!isShow">
        <div class="name">
          <span>{{username}}</span>
          <!-- |
          <span>0537</span> -->
        </div>
      </div>
      <div class="content">
        <div>
          <i>考试名称:</i>
          <span>{{name}}</span>
        </div>
        <div>
          <i>考试类型:</i>
          <span>{{specialCode}}</span>
        </div>
        <div>
          <i>考试时间:</i>
          <span>{{duration}}分钟</span>
        </div>
        <div>
          <i>合格分数:</i>
          <span>{{passScore}}分</span>
        </div>
        <div class="notice">
          <i>注意事项:</i>
          <span>{{rule}}</span>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="startTest" @click.stop="startTest()" v-if='this.startTime <= new Date().getTime()'><span>开始考试</span></div>
      <div class="noStartText" v-if='this.startTime > new Date().getTime()'><span>暂未开始</span></div>
    </div>
    <div class='bigBox'>
      <div class='smallBox'>
        <img src="../../assets/image/开始考试的背景.png" alt="">
        <div class='boxWord'>确定开始考试?</div>
        <div class='takepicture'>* 点击开始考试将会启动手机拍照</div>
        <div class="text-list-start-startText-mess-two">
            <div class="text-list-start-startText-mess-two-wait" @click="cancel">再等等</div>
            <div style="width:0.021rem;height:0.42rem;background:rgba(231,236,244,1);margin-top:0.3rem;"></div>
            <div class="text-list-start-startText-mess-two-go" @click="comfirm">开始考试</div>
        </div>
      </div>
    </div>
    <!-- 加载页面 -->
    <Loading :dialogVisible="verDialogVisible"></Loading>
    <!-- 确认弹出框 - 提示错误信息 -->
    <IsBox :dialogVisible="IsBoxDialogVisible" :mess="mess"  @inNesclose="IsBoxcloseVerDialogMethod"></IsBox>

  </div>
</template>

<script>
import API from '@/api/api'
import $ from 'jquery'
import Loading from './../components/loading'
import IsBox from './../components/isBox'
export default {
  name: 'index',
  components: {
    Loading,
    IsBox
  },
  data () {
    return {
      mess: '', // 错误
      id: '', // 考试id
      name: '', // 考试名称
      username: '', // 用户名
      duration: '', // 考试时长
      passScore: '', // 通过分数
      rule: '', // 考试注意事项
      selfExamId: '', // 个人考试id
      specialCode: '', // 考试类型
      head: '',
      isShow: true,
      startTime: '', // 考试开始时间
      verDialogVisible: true, // 加载页面
      IsBoxDialogVisible: false, // 是否打开提示信息
      specialCodeList: [] // 专项类型接口
    }
  },
  created () {
    localStorage.removeItem('resumeNum')
    this.getClassifyList()
    this.getPerAndOrgByUserId()
    this.selfExamId = this.$route.params.selfExamId// 接收个人考试id
    this.startTime = this.$route.params.startTime // 接收考试开始时间
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  methods: {
    // 返回键
    onBackKeyDown () {
      if ($('.text_list_start .bigBox').css('display') === 'flex') {
        $('.text_list_start .bigBox').css('display', 'none')
      } else {
        this.back()
      }
    },
    // 获取专题分类的接口
    getClassifyList () {
      API.post('getClassifyList', {
        dicId: this.GLOBAL.SPECIALCODE.dicId
      }, 'json', (data, err) => {
        if (data.success) {
          this.specialCodeList = data.data
          this.getDatatext()
        }
      })
    },
    // 获取考试个人信息
    getDatatext () {
      this.id = this.$route.params.id
      API.post('getExamInfo', {
        examId: this.id
      }, 'json', (data, err) => {
        if (data == null) {
          alert(err)
          return
        }
        if (data.success) {
          this.name = data.data[0].examPlanEntity.name
          this.duration = data.data[0].examPlanEntity.duration
          this.passScore = data.data[0].examPlanEntity.passScore
          this.rule = data.data[0].examPlanEntity.rule
          for (var i = 0; i < this.specialCodeList.length; i++) {
            if (data.data[0].examPlanEntity.specialCode === this.specialCodeList[i].code) {
              this.specialCode = this.specialCodeList[i].name
            }
          }
          this.verDialogVisible = false
        }
      })
    },
    // 获取个人信息
    getPerAndOrgByUserId () {
      API.post('getPerAndOrgByUserId', {
      }, 'json', (data, err) => {
        if (data.success) {
          this.username = data.data.personnel.name
          if (data.data.personnel.head != null && data.data.personnel.head !== '') {
            this.head = data.data.personnel.head
            this.isShow = true
          } else {
            this.head = '../../assets/image/头像.png'
            this.isShow = false
          }

          // console.log(this.head)
        }
      })
    },
    // 关闭错误弹窗
    IsBoxcloseVerDialogMethod () {
      this.IsBoxDialogVisible = false
      // this.$router.back();
    },
    // 返回
    back () {
      this.$router.back()
    },
    // 开始考试按钮
    startTest () {
      $('.bigBox').css('display', 'flex')
    },
    // 取消按钮
    cancel () {
      $('.bigBox').css('display', 'none')
    },
    // 确定按钮
    comfirm () {
      $('.bigBox').css('display', 'none')
      navigator.camera.getPicture(this.onSuccess, this.onFail, {
        quality: 50,
        // eslint-disable-next-line no-undef
        destinationType: Camera.DestinationType.FILE_URI,
        // cameraDirection:Camera.Direction.FRONT,
        correctOrientation: true,
        cameraDirection: 1,
        sourceType: 1
      })
    },
    // 拍照成功
    onSuccess (imageURI) {
      // alert(imageURI)
      const that = this
      that.verDialogVisible = true
      // eslint-disable-next-line no-undef
      var options = new FileUploadOptions()
      options.fileKey = 'file'
      // if(imageURI.indexOf("content:")>=0){
      //     options.fileName="random.jpg"
      // }else{
      options.fileName = imageURI.substring(imageURI.lastIndexOf('/') + 1)
      // }
      options.mimeType = 'image/jpeg'
      options.httpMethod = 'POST'
      // options.url = 'http://http://192.168.2.168:18082';
      // options.appId = 'a357b49f-dead-4821-94f2-f84bfa600286';
      // options.timestamp = Date.now();
      let sha256 = require('js-sha256').sha256
      options.params = { // 请求参数 键值对
        url: this.GLOBAL.UPLOAD.UPLOAD_URL,
        appId: this.GLOBAL.UPLOAD.UPLOAD_APPID,
        timestamp: Date.now(),
        sig: sha256(this.GLOBAL.UPLOAD.UPLOAD_APPID + this.GLOBAL.UPLOAD.UPLOAD_SECRET + Date.now())
      }
      // alert(options);
      // eslint-disable-next-line no-undef
      var ft = new FileTransfer()
      ft.upload(
        imageURI,
        encodeURI(`${this.GLOBAL.UPLOAD.UPLOAD_URL}/pxks/upload`),
        (r) => {
          // eslint-disable-next-line no-eval
          var data = eval('(' + r.response + ')').data
          that.head = `${this.GLOBAL.UPLOAD.UPLOAD_URL}/pxks/dl/` + data
          API.post('uploadPhoto', {
            selfId: that.selfExamId,
            imgOne: that.head
          }, 'json', (data, err) => {
            if (data.success) {
              // alert('上传成功')
              that.verDialogVisible = false
              that.enterMain()
            } else {
              // console.log(data.msg)
              that.verDialogVisible = false
              this.IsBoxDialogVisible = true
              this.mess = '网络异常，请重新进入'
            }
          })
        },
        (error) => {
          console.log(error)
          that.verDialogVisible = false
          this.IsBoxDialogVisible = true
          this.mess = '网络异常，请重新进入'

          // alert('Error uploading file ' + path + ': ' + error.code);
          // navigator.camera.cleanup(null,null);
        },
        options
      )
    },
    enterMain () {
      this.$router.replace({
        name: 'text_main',
        params: {
          id: this.id,
          duration: this.duration,
          selfExamId: this.selfExamId
        }
      })
      $('.bigBox').css('display', 'none')
    },
    onFail: function (message) {
      console.log('Failed because: ' + message)
    }

  }
}
</script>

<style scoped>
  .text_list_start{
    width:100%;
    height:100%;
    background:rgba(255,255,255,1);
  }
  .header{
    width:100%;
    background:#fff;
    font-size:0.36rem;
    font-family: PingFang-SC-Medium;
    position: fixed;
    top:0;
    height: 0.94rem;
    text-align: center;
  }
  .testHead{
    line-height: 0.94rem;
    font-size:0.36rem;
    color:#4E4E4E;
  }
  .back{
    width:0.68rem;
    height:0.94rem;
    position: fixed;
    top:0;
    left:0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .back img{
    width:0.2rem;
    height:0.36rem;
  }
  .contain{
    padding:0 0.72rem;
    width: 100%;
    margin:0.94rem 0 1.6rem 0;
    height:calc(100vh - 1.28rem - 1.66rem);
    overflow-Y: scroll;
    -webkit-overflow-scrolling: touch;
  }
  .head{
    text-align:center ;
  }
  .head img{
    width:1.4rem;
    height: 1.4rem;
    border-radius: 50%;
    margin-top:6.6717vh;
  }
  .head .name{
    font-size: 0.34rem;
    color:#333333;
    margin-top:2.8486vh;
  }
  .content{
    margin-top:6.7466vh;
    font-size:0.32rem;
  }
  .content div{
    display: flex;
    width:100%;
    margin-bottom: 3.2234vh;
  }
  .content i{
    display: block;
    color:#999999;
    font-family:PingFang-SC-Regular;
    line-height:0.45rem ;
    width:1.6rem;
  }
  .content span{
    display: block;
    width:calc(100vw - 1.36rem - 1.44rem - 0.22rem);
    color:#333333;
    font-family:PingFang-SC-Regular;
    line-height:0.45rem ;
    word-break: break-word;
  }
  .bottom{
    width:100%;
    padding:0.4rem 0.8rem;
    position:fixed;
    bottom:0;
    left:0;
  }
  .startTest{
    width: 100%;
    display: block;
    z-index: 5;
    outline: none;
    height:0.8rem;
    font-size: 0.3rem;
    color:#04CD9D;
    background: #D9FAF2;
    border:0.012rem solid #04CD9D;
    border-radius: 0.4rem;
    text-align: center;
  }
  .startTest span{
    line-height: 0.8rem;
    font-size:0.3rem;
  }
  .noStartText{
    width: 100%;
    display: block;
    z-index: 5;
    outline: none;
    height:0.8rem;
    font-size: 0.3rem;
    color:#C2CBD6;
    background: #E7ECF4;
    border:0.012rem solid #C2CBD6;
    border-radius: 0.4rem;
    text-align: center;
  }
  .noStartText span{
    line-height: 0.8rem;
    font-size:0.3rem;
  }
  .bigBox{
    z-index: 99;
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.7);
    position: fixed;
    top:0;
    display: none;
    justify-content: center;
    align-items: center;
  }
  .smallBox{
    position: relative;
    z-index: 999;
    width:5.5rem;
    height:2.74rem;
  }
  .smallBox img{
    width:5.5rem;
    height:2.74rem;
  }
  .smallBox .boxWord{
    position:absolute;
    width:100%;
    /* height:1.74rem; */
    display: flex;
    justify-content: center;
    /* align-items: center; */
    text-align: center;
    top:0.58rem;
    left:0;
    z-index: 1000;
    font-size:0.32rem;
    color:#fff;
    letter-spacing:0.02rem;
    color: rgba(78,78,78,1);
  }
  .smallBox .takepicture{
    position:absolute;
    width:100%;
    /* height:1.74rem; */
    display: flex;
    justify-content: center;
    /* align-items: center; */
    text-align: center;
    top:1.25rem;
    left:0;
    z-index: 1000;
    font-size:0.26rem;
    letter-spacing:0.01rem;
    color: #FB6C6C;
  }
  .smallBox .button{
    width:100%;
    height:0.78rem;
    position:absolute;
    bottom:0.5rem;
    left:0;
    text-align:center;
    z-index:1000;
    padding:0 0.4rem;
    display: flex;
    justify-content: space-between
  }

  .button div{
    width:2.18rem;
    height:0.78rem;
    /* display: inline-block; */
    font-size:0.3rem;

  }
  .button .cancel{
    color:#666666;
    background:#ffffff;
    border:0.012rem solid rgba(221,221,221,1);
    border-radius: 0.39rem;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top:0.045rem;
  }
  .button .comfirm{
    color:#ffffff;
    background:#04CD9D ;
    border-radius: 0.39rem;
    border:0.012rem solid #04CD9D;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top:0.045rem;
  }
  .text-list-start-startText-mess-two{
    width: 100%;
    height: 1rem;
    position: absolute;
    z-index: 999;
    display: flex;
    justify-content: space-around;
    align-content: center;
    bottom: 0;
    font-size: 0.34rem;
    border-top: 0.012rem solid #E7ECF4;
  }
  .text-list-start-startText-mess-two-wait{
    width: 48%;
    height: 100%;
    color:rgba(110,116,126,1);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.34rem;
  }
  .text-list-start-startText-mess-two-go{
    width: 48%;
    height: 100%;
    color:rgba(87, 148, 246, 1);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.34rem;
  }

</style>



